@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.header {
  background: #1403443f;
  text-align: center;
  height: 225px;
  padding-top: 70px;
  color: rgb(38, 57, 118);
  font-family: 'courier', sans-serif;
}

.search-text {
  font-size: large;
  font-family: 'Courier New', Courier, monospace;
}

.search-bar {
  margin-top: 100px;
  margin-left: 280px;
  text-align: match-parent;
  font-family: 'Courier New', Courier, monospace;
}
.table-employee {
  margin-top: 25px;
}

.table {
  width: 70% !important; 
  margin: auto;
  text-align: center;
  font-family: 'Courier New', Courier, monospace;
}

th:nth-child(1) {
  width: 5%;
}

th:nth-child(2) {
  width: 25%;
}
th:nth-child(3) {
  width: 20%;
}
th:nth-child(4) {
  width: 30%;
}
th:nth-child(5) {
  width: 20%;
}

.pointer {
  cursor: pointer;
}

.pointer:hover > .icon {
  visibility: visible;
}

.icon {
  visibility: hidden;
}

.rotate-false {
  color: red;
}

.rotate-true {
  color: blue;
  transform: rotate(180deg);
}


